<!--eslint-disable-->
<template>
  <div class="servicos-wrapper">
    <div class="cards-wrapper">
      <div class="card movement" v-for="s in servicos" :key="s.id">
        <h3 class="card-title">{{ s.title }}</h3>
        <div class="card-copy">
          <span v-html="s.text"></span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import axios from "axios";

export default {
  data() {
    return {
      servicos: [
        {
          id: "",
          title: "",
          subtext: "",
          text: "",
        },
      ],
    };
  },
  methods: {
    FadeIn() {
      var allMods = $(".movement, .cover");
      var delayedMods = $(".movement-delayed");
      var lateralMods = $(".lateral, .lateralR");
      var svg = $("#svg");

      (function ($) {
        $.fn.visible = function (partial, adjust) {
          if (adjust === undefined) {
            adjust = 0;
          }
          var elem = $(this),
            wind = $(window),
            viewTop = wind.scrollTop(),
            viewBottom = viewTop + wind.height(),
            elem_top = elem.offset().top + adjust,
            elem_bottom = elem_top + elem.height() - adjust,
            compareTop = partial === true ? elem_bottom : elem_top,
            compareBottom = partial === true ? elem_top : elem_bottom;

          return compareBottom <= viewBottom && compareTop >= viewTop;
        };
      })(jQuery);

      var win = $(window);

      var xmlTeste = true;

      allMods.each(function (i, el) {
        var el = $(el);
        if (el.visible(true)) {
          el.addClass("placed");
        }
      });
      svg.each(function (i, el) {
        var el = $(el);
        if (el.visible(true)) {
          xmlTeste &&
            el.append(
              "<img src='assets/img/elemento_animated.svg' alt=''></img>"
            );
          xmlTeste = false;
        }
      });
      lateralMods.each(function (i, el) {
        var el = $(el);
        if (el.visible(true, 100)) {
          el.addClass("placed");
        }
      });

      delayedMods.each(function (i, el) {
        var el = $(el);
        if (el.visible(true, 800)) {
          el.addClass("placed");
        }
      });

      win.scroll(function (event) {
        allMods.each(function (i, el) {
          var el = $(el);
          if (el.visible(true)) {
            el.addClass("placed");
          }
        });
        lateralMods.each(function (i, el) {
          var el = $(el);
          if (el.visible(true, 100)) {
            el.addClass("placed");
          }
        });
        delayedMods.each(function (i, el) {
          var el = $(el);
          if (el.visible(true, 800)) {
            el.addClass("placed");
          }
        });
        svg.each(function (i, el) {
          var el = $(el);
          if (el.visible(true)) {
            xmlTeste &&
              el.append(
                "<img src='assets/img/elemento_animated.svg' alt=''></img>"
              );
            xmlTeste = false;
          }
        });
      });
    },
  },
  mounted() {
    var map = Array.prototype.map;
    var vm = this;

    axios
      .get("https://beta.anacarolinapereira.pt/db-api/getAbout.php")
      .then((resp) => {
        const data = resp.data;
        var result = data.split("||");
        var servicos = JSON.parse(result[2]);
        var servicosArray = [];

        map.call(servicos, function (s) {
          servicosArray.push({
            id: s.id,
            title: s.title,
            subtext: s.subtext,
            text: s.text,
          });
        });

        vm.$data.servicos = servicosArray;
      })
      .then(() => {
        this.FadeIn();
      });
  },
};
</script>

<style lang="scss" scoped>
//colors
$white: #fff;
$black: #000;
$dk-gray-1: #393939;
$dk-gray-2: #464646;
$dk-gray-3: #707070;
$lt-gray: #f6f6f6;
$turquiose: #0babc5;

//paddings and margins
$pd-desktop: 0 130px;
$mg-desktop: 0 40px;

//circle
$circle: 50%;

@function pxToEm($value) {
  $point: ($value * 3) / 4;
  $yem: $point / 12;

  @return $yem * 1rem;
}

.servicos-wrapper {
  height: auto;
  background-color: $white;
  max-width: pxToEm(1500);
  margin: auto;

  @media only screen {
    height: auto;
  }

  .cards-wrapper {
    display: flex;
    // max-width: pxToEm(1400);
    width: 100%;
    justify-content: space-between;
    margin-bottom: pxToEm(80);
    height: 55%;
    flex-wrap: wrap;

    @media only screen and (min-width: 600px) and (max-width: 991px) {
      justify-content: center;
    }

    @media only screen and (min-width: 300px) and (max-width: 599px) {
      flex-direction: column;
      height: 100%;
      align-items: center;
      margin-bottom: pxToEm(40);
      justify-content: center;
    }

    .card {
      width: pxToEm(600);
      height: pxToEm(220);
      // height: auto;
      background-color: $lt-gray;
      border-radius: pxToEm(23);
      display: flex;
      flex-direction: column;
      align-items: center;
      // justify-content: space-between;
      padding: pxToEm(50) pxToEm(50);
      cursor: pointer;
      transition: 0.5s;
      // margin: pxToEm(50) pxToEm(10) 0 pxToEm(10);
      margin-bottom: pxToEm(50);

      @media only screen and (min-width: 600px) and (max-width: 1199px) {
        margin-bottom: pxToEm(30);
      }

      @media only screen and (min-width: 300px) and (max-width: 599px) {
        margin: pxToEm(10) 0;
        width: 80%;
        height: auto;
        padding: pxToEm(30) pxToEm(40);
      }

      .icon {
        width: pxToEm(80);
        height: pxToEm(80);
        border-radius: $circle;
        overflow: hidden;
        background-size: cover;

        @media only screen and (min-width: 300px) and (max-width: 599px) {
          width: pxToEm(70);
          height: pxToEm(70);
        }
      }

      .card-title {
        margin: 0;
        font-family: Arial, Helvetica, sans-serif;
        font-size: pxToEm(28);
        color: $turquiose;
        font-weight: bold;
        text-align: center;
        margin-bottom: pxToEm(15);
      }

      .card-copy {
        font-size: pxToEm(22);
        color: #a4a4a4;
        text-align: justify;
        text-align-last: center;

        i {
          display: inline-block;
          margin-bottom: pxToEm(15);
          font-family: Georgia, "Times New Roman", Times, serif;
        }
      }

      a {
        align-self: flex-end;

        .cta {
          width: pxToEm(80);
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          align-self: flex-end;
          align-items: center;

          @media only screen and (min-width: 300px) and (max-width: 599px) {
            width: pxToEm(100);
            align-self: center;
          }

          .txt {
            font-family: "OS-Regular";
            color: #a4a4a4;
            text-transform: uppercase;
            font-size: pxToEm(14);

            @media only screen and (min-width: 300px) and (max-width: 599px) {
              color: $turquiose;
            }
          }

          .arrow-cont {
            position: relative;
            transition: 0.5s;

            .arrow {
              border: solid #a4a4a4;
              border-width: 0 pxToEm(1) pxToEm(1) 0;
              display: inline-block;
              padding: pxToEm(2);
              position: absolute;
              top: pxToEm(-2);
              left: pxToEm(4);

              &.right {
                transform: rotate(-45deg);
                -webkit-transform: rotate(-45deg);
              }
            }
          }

          svg {
            width: pxToEm(11);
            height: pxToEm(11);
            overflow: visible;

            .arrow {
              fill: #a4a4a4;
              transition: 0.5s;

              @media only screen and (min-width: 300px) and (max-width: 599px) {
                fill: $turquiose;
              }
            }
          }
        }
      }
    }
  }

  .servicos {
    // margin-bottom: pxToEm(70);
    display: flex;
    height: 100%;
    justify-content: space-between;

    @media only screen and (max-width: 991px) {
      flex-direction: column;
    }

    .servico {
      // padding: pxToEm(60) pxToEm(80);
      width: 50%;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: space-between;

      &:first-child {
        padding: pxToEm(60) pxToEm(80) pxToEm(60) pxToEm(630);
      }

      &:last-child {
        padding: pxToEm(60) pxToEm(630) pxToEm(60) pxToEm(80);
      }

      @media only screen and (min-width: 1200px) {
        padding: pxToEm(40) pxToEm(60);
      }

      @media only screen and (min-width: 992px) and (max-width: 1199px) {
        padding: pxToEm(40) pxToEm(60);
      }

      @media only screen and (max-width: 991px) {
        width: auto;
        padding: pxToEm(40);
        align-items: center;
      }

      @media only screen and (max-width: 599px) {
        text-align: center;
        align-items: center;
      }

      &:nth-child(2) {
        background-color: $turquiose;

        .txt-big {
          color: $white;
          font-family: "OS-Bold";
        }

        .line {
          background-color: $white;
        }

        .copy {
          color: $white;
          font-family: "OS-Regular";
        }

        .topics {
          color: $white;

          .topic {
            color: $white;
          }
        }
      }

      .txt-big {
        font-size: pxToEm(28);
        color: $dk-gray-3;
        font-family: "OS-Bold";
        margin-bottom: pxToEm(15);

        @media only screen {
          margin-bottom: pxToEm(20);
        }
      }

      .line {
        height: pxToEm(1);
        width: pxToEm(80);
        background-color: $turquiose;
        margin-bottom: pxToEm(20);

        @media only screen {
          margin-bottom: pxToEm(20);
        }
      }

      .copy {
        color: $dk-gray-3;
        width: 100%;
        font-size: pxToEm(22);
        font-family: "OS-Regular";
        margin-bottom: pxToEm(20);

        @media only screen {
          margin-bottom: pxToEm(20);
        }

        @media only screen and (max-width: 991px) {
          text-align: center;
        }
      }

      .topics {
        display: flex;
        flex-direction: column;
        font-family: "OS-Regular";
        font-size: pxToEm(20);

        @media only screen and (max-width: 991px) {
          text-align: center;
        }
      }
    }
  }
}
</style>



